<template>
	<div>
		<header
			style="display: flex; justify-content: center; align-items: center"
		>
			<h1 style="margin: 0px; text-align: center">
				TERA Card Calculator
			</h1>
		</header>
		<div id="mainWrapper">
			<section id="main">
				<Card
					v-for="(card, key) in $options.cards"
					v-bind:key="key"
					:cost="card.cost"
					:name="key"
					:category="card.category"
					:type="card.type"
					:stats="{
						White: card.White || null,
						Green: card.Green || null,
						Blue: card.Blue || null,
					}"
				></Card>
			</section>

			<aside id="statInput">
				<h4>PLAYER STATS</h4>
				<form action="">
					<label for="playerPower">Power: </label
					><input
						type="number"
						name="playerPower"
						v-model.number="playerPower"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerMAmp">Magic Amplification: </label
					><input
						type="number"
						name="playerMAmp"
						v-model.number="playerMAmp"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerPAmp">Physical Amplification: </label
					><input
						type="number"
						name="playerPAmp"
						v-model.number="playerPAmp"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerCp">Crit Power: </label
					><input
						type="number"
						step="0.01"
						name="playerCp"
						v-model.number="playerCp"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerMCp">Magic Crit Power: </label
					><input
						step="0.01"
						type="number"
						name="playerMCp"
						v-model.number="playerMCp"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerPCp">Physical Crit Power: </label
					><input
						step="0.01"
						type="number"
						name="playerPCp"
						v-model.number="playerPCp"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerDmgFromCrit"
						>% of Damage done by Crits: </label
					><input
						type="number"
						step="0.01"
						name="playerDmgFromCrit"
						v-model.number="playerDmgFromCrit"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerMMod">Skill Magic Modifier: </label
					><input
						type="number"
						step="0.01"
						name="playerMMod"
						v-model.number="playerMMod"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerPMod">Skill Physical Modifier: </label
					><input
						type="number"
						step="0.01"
						name="playerPMod"
						v-model.number="playerPMod"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerPierce">Piercing: </label
					><input
						type="number"
						name="playerPierce"
						v-model.number="playerPierce"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerIgnore">Ignore Resistance: </label
					><input
						type="number"
						name="playerIgnore"
						v-model.number="playerIgnore"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="partyShred">Party Resistance Shred: </label
					><input
						type="number"
						name="partyShred"
						v-model.number="partyShred"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="playerCollectionLevel">Collection Level: </label
					><select
						name="playerCollectionLevel"
						v-model="playerCollectionLevel"
						v-on:change="writeStatsToLocalStorage"
					>
						<option
							v-for:="(level, key) in $options.collectionLevels"
							v-bind:key="key"
							:value="level"
						>
							{{ key }}
						</option>
					</select>
				</form>

				<h4>TARGET</h4>
				<form>
					<label for="bossBaseRes">Boss Base Resistance: </label
					><input
						type="number"
						name="bossBaseRes"
						v-model.number="bossBaseRes"
						v-on:change="writeStatsToLocalStorage"
					/>

					<label for="bossType">Boss Type: </label
					><select
						name="bossType"
						v-model="bossType"
						v-on:change="writeStatsToLocalStorage"
					>
						<option
							v-for="type in $options.bossTypes"
							v-bind:key="type"
							:value="type"
						>
							{{ type }}
						</option>
					</select>
				</form>
				<hr />
				<label for="ignoreCritCards">Ignore Crit Factor Cards</label>
				<input
					type="checkbox"
					name="ignoreCritCards"
					id="ignoreCritCards"
					v-model="ignoreCritCards"
					v-on:change="writeStatsToLocalStorage"
				/>
				<br />
				<button v-on:click="calculateBestCards">Calculate</button>
				<hr />
				<section id="results">
					<h4>OPTIMAL SETUP:</h4>
					<p v-if="$data.bestCards.length == 0" style="padding: 1rem">
						Calculating best setup might take a few seconds... don't
						worry, the site didn't freeze.
					</p>
					<p
						v-for="cardName in $data.bestCards"
						:key="cardName"
						:style="getSelectedCardrarity(cardName)"
					>
						{{ cardName }}
					</p>
					<p v-if="$data.bestCards.length != 0">
						DPS gained:
						{{
							Math.round(
								10000 * $data.bestCards.totalDpsGain +
									Number.EPSILON
							) / 100
						}}%
					</p>
				</section>
			</aside>
		</div>
	</div>
</template>

<script>
import Card from "./components/Card.vue";
import cards from "./data/cards.json";
import bossTypes from "./data/types.json";
import collectionLevels from "./data/collectionLevels.json";

import cardStore from "./Stores/CardStore";

import G from "./util/generatorics";

export default {
	name: "App",
	components: {
		Card,
	},
	data() {
		for (const name of Object.keys(cards)) {
			cardStore.cards.set(name, {
				enabled: true,
				rarity: "Green",
			});
		}
		return {
			playerPower: 0,
			playerCp: 0,
			playerMCp: 0,
			playerPCp: 0,
			playerMAmp: 0,
			playerPAmp: 0,
			playerPierce: 0,
			playerIgnore: 0,

			playerMMod: 0,
			playerPMod: 0,

			partyShred: 0,
			bossBaseRes: 0,
			playerDmgFromCrit: 0,
			ignoreCritCards: false,
			bossType: "",

			playerCollectionLevel: {
				maxPoints: 9,
				maxCardsPerCategory: 1,
				maxAmountCards: 2,
			},
			bestCards: [],
			cardStore,
			calculating: false,
		};
	},
	//TODO: once mounted, check local storage for stats, selected rarities and disabled cards
	mounted() {
		if (localStorage.getItem("stats")) {
			try {
				for (let [stat, value] of Object.entries(
					JSON.parse(localStorage.getItem("stats"))
				)) {
					this[stat] = value;
				}
			} catch (e) {
				localStorage.removeItem("stats");
			}
		}
	},
	methods: {
		writeStatsToLocalStorage() {
			let stats = {
				playerPower: this.playerPower,
				playerCp: this.playerCp,
				playerMCp: this.playerMCp,
				playerPCp: this.playerPCp,
				playerMAmp: this.playerMAmp,
				playerPAmp: this.playerPAmp,
				playerPierce: this.playerPierce,
				playerIgnore: this.playerIgnore,

				playerMMod: this.playerMMod,
				playerPMod: this.playerPMod,

				partyShred: this.partyShred,
				bossBaseRes: this.bossBaseRes,
				playerDmgFromCrit: this.playerDmgFromCrit,
				ignoreCritCards: this.ignoreCritCards,
				bossType: this.bossType,

				playerCollectionLevel: this.playerCollectionLevel,
			};
			localStorage.setItem("stats", JSON.stringify(stats));
		},
		getSelectedCardrarity(cardName) {
			let colours = {
				White: "#E8E6E3",
				Green: "#29C75D",
				Blue: "#44CBFC",
			};
			return `color: ${colours[cardStore.cards.get(cardName).rarity]}`;
		},
		calculateBestCards() {
			this.calculating = true;
			if (this.playerDmgFromCrit > 1) this.playerDmgFromCrit /= 100;
			if (this.playerPMod > 4) this.playerPMod /= 100;
			if (this.playerMMod > 4) this.playerMMod /= 100;
			this.bestCards = [];
			//STEP 0: calculate bosses final resistance value
			const finalBossResist = Math.max(
				(this.bossBaseRes - this.partyShred - this.playerIgnore) *
					(1 -
						this.calcPiercePercent(
							this.playerPierce,
							this.bossBaseRes -
								this.partyShred -
								this.playerIgnore
						)),
				-33333
			);

			//STEP 1, grab subset of cards that are not disabled and fit the specified monster type
			const consideredCards = {};
			cardStore.cards.forEach((card, key) => {
				if (
					(card.enabled &&
						this.bossType === "" &&
						!cards[key].type) ||
					(card.enabled &&
						this.bossType !== "" &&
						this.bossType === cards[key].type) ||
					(card.enabled &&
						this.bossType !== "" &&
						!cards[key].type &&
						!(
							this.ignoreCritCards &&
							"cf" in cards[key][card.rarity]
						))
				) {
					consideredCards[key] = cards[key];
				}
			});
			//Step 2 ish, sort cards by their categories
			const cardsPerCategory = {
				Monster: [],
				Fish: [],
				Location: [],
				Character: [],
				Mount: [],
			};
			for (let category of [
				"Monster",
				"Fish",
				"Location",
				"Character",
				"Mount",
			]) {
				for (let [key, card] of Object.entries(consideredCards)) {
					if (card.category === category)
						cardsPerCategory[category].push(key);
				}
			}
			const subsetsPerCategory = {
				Monster: [],
				Fish: [],
				Location: [],
				Character: [],
				Mount: [],
			};
			for (let category in cardsPerCategory) {
				for (
					let i = 0;
					i <= this.playerCollectionLevel.maxCardsPerCategory;
					i++
				) {
					for (let combination of G.clone.combination(
						cardsPerCategory[category],
						i
					)) {
						subsetsPerCategory[category].push(combination);
					}
				}
			}
			const bigCartesian = require("big-cartesian");
			const fastCartesian = require("fast-cartesian");
			/*
			the Monster array is considerably larger than the others, so by taking a 
			fast cartesian product of it with a subset of the other categories we might 
			be able to save time
			*/
			let cartesian = [];
			// HEAVY WIZARDRY STARTS HERE
			for (let value of bigCartesian([
				fastCartesian([
					subsetsPerCategory.Monster,
					subsetsPerCategory.Fish,
					subsetsPerCategory.Location,
				])
					.filter((element) => {
						let totalSize = 0;
						for (let arr of element) {
							totalSize += arr.length;
						}
						return (
							totalSize <=
							this.playerCollectionLevel.maxAmountCards
						);
					})
					.map((element) => (element = element.flat())),
				subsetsPerCategory.Character,
				subsetsPerCategory.Mount,
			])) {
				let totalSize = 0;
				for (let arr of value) {
					totalSize += arr.length;
				}
				if (totalSize <= this.playerCollectionLevel.maxAmountCards)
					cartesian.push(value.flat());
			}

			//By this time, cards are filtered by max amount of cards in total and max amount per category, but they might still exceed the point limit, so filter them for that.
			cartesian = cartesian.filter((cardSetup) => {
				let pointCost = 0;
				for (let cardName of cardSetup) {
					pointCost += cards[cardName].cost;
				}
				return pointCost <= this.playerCollectionLevel.maxPoints;
			});

			//STEP 3, iterate over cartesian product and assign stat gains
			for (let subset of cartesian) {
				subset.totalBonusPamp = 0;
				subset.totalBonusMamp = 0;
				subset.totalBonusCf = 0;
				subset.totalBonusMcp = 0;
				subset.totalBonusPcp = 0;
				subset.totalBonusMp = 0;
				for (let cardName of subset) {
					/* eslint-disable no-mixed-spaces-and-tabs */
					subset.totalBonusPamp += cards[cardName].type
						? cards[cardName].type === this.bossType
							? cards[cardName][
									cardStore.cards.get(cardName).rarity
							  ].pAmp || 0
							: 0
						: cards[cardName][cardStore.cards.get(cardName).rarity]
								.pAmp || 0;

					subset.totalBonusMamp += cards[cardName].type
						? cards[cardName].type === this.bossType
							? cards[cardName][
									cardStore.cards.get(cardName).rarity
							  ].mAmp || 0
							: 0
						: cards[cardName][cardStore.cards.get(cardName).rarity]
								.mAmp || 0;

					subset.totalBonusMcp += cards[cardName].type
						? cards[cardName].type === this.bossType
							? cards[cardName][
									cardStore.cards.get(cardName).rarity
							  ].mCp || 0
							: 0
						: cards[cardName][cardStore.cards.get(cardName).rarity]
								.mCp || 0;

					subset.totalBonusPcp += cards[cardName].type
						? cards[cardName].type === this.bossType
							? cards[cardName][
									cardStore.cards.get(cardName).rarity
							  ].pCp || 0
							: 0
						: cards[cardName][cardStore.cards.get(cardName).rarity]
								.pCp || 0;

					subset.totalBonusCf +=
						cards[cardName][cardStore.cards.get(cardName).rarity]
							.cf || 0;
					/* eslint-enable no-mixed-spaces-and-tabs */
				}
			}
			//STEP 4, calculate dps gain for all valid sets
			let currentBestSubset = undefined;
			for (let subset of cartesian) {
				subset.totalDpsGain =
					(1 +
						this.calcDpsGainAmp(
							subset.totalBonusPamp,
							subset.totalBonusMamp,
							subset.totalBonusPcp,
							subset.totalBonusMcp,
							finalBossResist
						)) *
						(1 +
							(this.ignoreCritCards ? 0 : 1) *
								this.calcDpsGainCrit(subset.totalBonusCf)) -
					1;
				if (!currentBestSubset) currentBestSubset = subset;
				if (currentBestSubset.totalDpsGain < subset.totalDpsGain)
					currentBestSubset = subset;
			}
			this.calculating = false;
			this.bestCards = currentBestSubset;
		},
		calcPiercePercent(pierce, specialDef) {
			//Pierce does not affect damage if base - shred - ignore <= 0
			return specialDef > 0
				? Math.min(pierce / (pierce + 10000), 0.8)
				: 0;
		},
		calcDpsGainAmp(
			bonusPamp = 0,
			bonusMamp = 0,
			bonusPcp = 0,
			bonusMcp = 0,
			finalBossResist = 0
		) {
			/**
			 * for all that is holy, please let me never touch this unholy abomination again
			 */
			return (
				((1 +
					((this.playerPAmp + bonusPamp) * this.playerPMod) /
						(100000 + finalBossResist) +
					((this.playerMAmp + bonusMamp) * this.playerMMod) /
						(100000 + finalBossResist)) *
					(1 - this.playerDmgFromCrit) +
					(1 * this.playerCp * 0.9 +
						((this.playerPAmp + bonusPamp) *
							this.playerPMod *
							(this.playerPCp + bonusPcp)) /
							(100000 + finalBossResist) +
						((this.playerMAmp + bonusMamp) *
							this.playerMMod *
							(this.playerMCp + bonusMcp)) /
							(100000 + finalBossResist)) *
						this.playerDmgFromCrit) /
					((1 +
						(this.playerPAmp * this.playerPMod) /
							(100000 + finalBossResist) +
						(this.playerMAmp * this.playerMMod) /
							(100000 + finalBossResist)) *
						(1 - this.playerDmgFromCrit) +
						(1 * this.playerCp * 0.9 +
							(this.playerPAmp *
								this.playerPMod *
								this.playerPCp) /
								(100000 + finalBossResist) +
							(this.playerMAmp *
								this.playerMMod *
								this.playerMCp) /
								(100000 + finalBossResist)) *
							this.playerDmgFromCrit) -
				1
			);
		},

		calcDpsGainCrit(bonusCrit = 0) {
			return (
				(100 + this.playerPower + bonusCrit / 2) /
					(100 + this.playerPower) -
				1
			);
		},
	},
	cards,
	bossTypes,
	collectionLevels,
};
</script>

