<template>
	<div id="cardWrapper">
		<section id="raritySelector">
			<RaritySelector
				v-for="rarity in validRarities"
				v-bind:key="rarity"
				:rarity="rarity"
				v-on:changeRarity="handleRarityChange"
			></RaritySelector>
		</section>
		<section
			id="cardContainer"
			:style="cardState.enabled ? '' : 'filter: grayscale(100%)'"
			v-on:click="toggleState"
		>
			<h4 :style="getRarityColours">
				<span id="pointCost">{{ cost }}</span
				>{{ name }}
			</h4>
			<div
				v-if="stats[$data.cardState.rarity]"
				id="stats"
				:style="getRarityColours"
			>
				<p v-if="stats[$data.cardState.rarity].mAmp" class="stat">
					<span v-if="type" :style="'color:#81B380'"
						>[{{ type }}]</span
					>
					Magic Amplification:
					<span :style="'color:#4ECC30'">{{
						stats[$data.cardState.rarity].mAmp
					}}</span>
				</p>
				<p v-if="stats[$data.cardState.rarity].pAmp" class="stat">
					<span v-if="type" :style="'color:#81B380'"
						>[{{ type }}]</span
					>
					Physical Amplification:
					<span :style="'color:#4ECC30'">{{
						stats[$data.cardState.rarity].pAmp
					}}</span>
				</p>
				<p v-if="stats[$data.cardState.rarity].mCp" class="stat">
					<span v-if="type" :style="'color:#81B380'"
						>[{{ type }}]</span
					>
					Magic Crit Power:
					<span :style="'color:#4ECC30'">{{
						stats[$data.cardState.rarity].mCp
					}}</span>
				</p>
				<p v-if="stats[$data.cardState.rarity].pCp" class="stat">
					<span v-if="type" :style="'color:#81B380'"
						>[{{ type }}]</span
					>
					Physical Crit Power:
					<span :style="'color:#4ECC30'">{{
						stats[$data.cardState.rarity].pCp
					}}</span>
				</p>
				<p v-if="stats[$data.cardState.rarity].cf" class="stat">
					Crit Factor:
					<span :style="'color:#4ECC30'">{{
						stats[$data.cardState.rarity].cf
					}}</span>
				</p>
				<p v-if="stats[$data.cardState.rarity].mpInc" class="stat">
					Max MP:
					<span :style="'color:#4ECC30'">{{
						stats[$data.cardState.rarity].mpInc
					}}</span>
				</p>
			</div>
			<div class="shadow" :style="getRarityColours"></div>
		</section>
	</div>
</template>

<script>
import RaritySelector from "./RaritySelector";
import cardStore from "../Stores/CardStore";

export default {
	name: "Card",
	props: ["name", "cost", "category", "stats", "type"],
	components: {
		RaritySelector,
	},
	data() {
		let cardState = cardStore.cards.get(this.name);
		return {
			cardState,
		};
	},
	computed: {
		getRarityColours() {
			let colours = {
				White: {
					"--radial-start-colour": "#2E3947",
					"--radial-end-colour": "#18202C",
					"--linear-start-colour": "#262D3C",
					"--linear-end-colour": "#4D5361",
					"--shadow-colour": "#435360",
				},
				Green: {
					"--radial-start-colour": "#183c21",
					"--radial-end-colour": "#182821",
					"--linear-start-colour": "#1e372c",
					"--linear-end-colour": "#294939",
					"--shadow-colour": "#237813",
				},
				Blue: {
					"--radial-start-colour": "#184563",
					"--radial-end-colour": "#102843",
					"--linear-start-colour": "#1B3252",
					"--linear-end-colour": "#39597B",
					"--shadow-colour": "#1C64B3",
				},
			};
			return colours[this.cardState.rarity];
		},
		validRarities() {
			return Object.keys(this.stats).filter(
				(rarity) => this.stats[rarity] != null
			);
		},
	},
	mounted() {
		if (localStorage.getItem(this.name)) {
			try {
				this.cardState = JSON.parse(localStorage.getItem(this.name));
				cardStore.cards.set(this.name, this.cardState);
			} catch (e) {
				console.error(e);
				localStorage.removeItem(this.name);
			}
		}
	},
	methods: {
		toggleState() {
			this.cardState.enabled = !this.cardState.enabled;
			this.saveStateToLocalStorage();
		},
		handleRarityChange(newRarity) {
			this.cardState.rarity = newRarity;
			this.saveStateToLocalStorage();
		},
		saveStateToLocalStorage() {
			localStorage.setItem(this.name, JSON.stringify(this.cardState));
		},
	},
};
</script>

