<template>
	<div
		:style="getRaritySelectorColours(rarity)"
		v-on:click="$emit('changeRarity', this.rarity)"
	></div>
</template>

<script>
export default {
	name: "RaritySelector",
	props: ["rarity"],
	methods: {
		getRaritySelectorColours(rarity) {
			let colours = {
				White: "#E8E6E3",
				Green: "#29C75D",
				Blue: "#44CBFC",
			};
			return `background-color: ${colours[rarity]}`;
		},
	},
};
</script>

